import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion'
import logo from './logo.svg';
import codiny from './codiny2.svg';
import Podium from './Podium';
import podiumData from './data';

import { GoogleSpreadsheet } from 'google-spreadsheet';
import { JWT } from 'google-auth-library';
import creds from './sweat-2024-camp-47fb5683de5e.json'; // the file saved above

const App = () => {
  const [spreadsheetData, setSpreadsheetData] = useState([]);
  const [podiumData, setPodiumData] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      // Initialize auth - see https://theoephraim.github.io/node-google-spreadsheet/#/guides/authentication
      const serviceAccountAuth = new JWT({
        email: creds.client_email,
        key: creds.private_key,
        scopes: [
          'https://www.googleapis.com/auth/spreadsheets',
        ],
      });

      const doc = new GoogleSpreadsheet('1BRyi-nkvqLHntiU8-MoHKiHuBdRVwtLVn82gKqb4rAo', serviceAccountAuth);

      await doc.loadInfo(); // loads document properties and worksheets
      console.log(doc.title);

      const sheet = doc.sheetsByIndex[1];
      console.log(sheet.title);
      console.log(sheet.rowCount);
      // read rows
      const rows = await sheet.getRows(); // can pass in { limit, offset }
      // read/write row values
      console.log(rows[0].get('p1')); // 'Larry Page'
      const data = [];
      for (let i = 1; ; i++) {
        console.log(rows[i]);
        if (rows[i] === undefined) break;
        console.log(rows[i].get('name'));
        data.push({
          id: i,
          name: rows[i].get('name'),
          l1: rows[i].get('l1') == "" ? "":"✅",
          l2: rows[i].get('l2') == "" ? "":"✅",
          time: rows[i].get('time'),
          distance: rows[i].get('distance'),
          penalty: rows[i].get('penalty'),
          total: rows[i].get('total'),
          record: rows[i].get('record')

        });
      }
      setSpreadsheetData(data);
      // Sort the data by total score in descending order
      const sorted_data = [...data];
      sorted_data.sort((a, b) => b.total - a.total);

      // Get the top 3 teams
      const top3Teams = sorted_data.slice(0, 3);
      console.log(top3Teams);
      setPodiumData(top3Teams.map((winner, position) => ({ ...winner, position })));
    };

    fetchData();

    // Fetch data every 5 seconds
    const intervalId = setInterval(() => {
      fetchData();
    }, 5000);

    // Cleanup the interval when the component is unmounted
    return () => clearInterval(intervalId);
  }, []); // Empty dependency array ensures the effect runs once after the initial render

  return (
    <div className="flex flex-row relative w-screen h-screen box-border">
      <div className='h-full w-120 bg-primary'>
        <h1 className='text-3xl mt-8 text-center text-accent'>SWeat 동계캠프</h1>
        <h1 className='text-4xl text-center text-white'>AI 자동차 대회</h1>
        <Podium winners={podiumData} />
        <div className="absolute left-4 bottom-4 w-60 scale-m">
          <img className="max-w-full" src={codiny}></img>
        </div>
      </div>
      <div className='flex-grow w-max h-full bg-secondary'>
        <div className='flex flex-col justify-center'>
          <div className='flex bg-white drop-shadow-lg px-8 py-2 flex-row'>
            <div className='w-16 bg-primary rounded-3xl text-white my-1 mx-4 flex justify-center items-center text-xl text-center'>팀</div>
            <div className='flex-1 flex justify-center items-center text-2xl text-accent border-l-2 border-opacity-50'>후진등</div>
            <div className='flex-1 flex justify-center items-center text-2xl text-accent border-l-2 border-opacity-50'>터널라이트</div>
            <div className='flex-1 flex justify-center items-center text-2xl text-accent border-l-2 border-opacity-50'>도착 구간</div>
            <div className='flex-1 flex justify-center items-center text-2xl text-accent border-l-2 border-opacity-50'>시간 점수</div>
            <div className='flex-1 flex justify-center items-center text-2xl text-accent border-l-2 border-opacity-50'>페널티</div>
            <div className='flex-1 flex justify-center items-center text-2xl text-white bg-primary rounded-lg'>총점</div>
            <div className='flex-1 flex justify-center items-center text-2xl text-accent border-l-2 border-opacity-50'>기록</div>
          </div>
          {spreadsheetData.map((rowData, index) => (
            <motion.div 
            animate={{ scale: [1, 1.1, 1]}} // scale과 opacity의 애니메이션을 배열로 정의하여 반복
        transition={{
          repeat: Infinity, // 무한 반복 설정
          repeatDelay: 8, // 5초 주기로 반복
          delay: index * (0.8), // Stagger the start time based on the index
        }}
            key={index} className={`flex ring-1 ring-gray-50 rounded-lg bg-white filter shadow mx-8 my-2 flex-row transition-all hover:shadow-md`}>
              <div className={`w-16 leading-7 h-16 bg-primary rounded-3xl text-white my-2 mx-4 flex justify-center items-center text-2xl text-center ${index % 3 === 1 ? 'bg-a' : index % 3 === 2 ? 'bg-b' : 'bg-c'}`}>{rowData.name}</div>
              <div className='flex-1 flex justify-center items-center text-4xl text-accent'>{rowData.l1}</div>
              <div className='flex-1 flex justify-center items-center text-4xl text-accent'>{rowData.l2}</div>
              <div className='flex-1 flex justify-center items-center text-4xl text-accent'>{rowData.distance}</div>
              <div className='flex-1 flex justify-center items-center text-4xl text-accent'>{rowData.time}</div>
              <div className='flex-1 flex justify-center items-center text-4xl text-accent'>-{rowData.penalty}</div>
              <div className='flex-1 flex justify-center items-center text-4xl text-white bg-primary rounded-lg'>{rowData.total}</div>
              <div className='flex-1 flex justify-center items-center text-3xl text-accent'>{rowData.record}</div>

            </motion.div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default App;
