import { motion } from 'framer-motion'
export default function PodiumStep({ podium, winner }) {
  const offset = podium.length - winner.position
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        placeContent: 'center',
        alignItems: 'center'
      }}
    >
      <motion.div
        style={{
          alignSelf: 'center',
          marginBottom: '.25rem',
        }}
        initial="hidden"
        animate="visible"
        variants={{
          hidden: { opacity: 0 },
          visible: {
            opacity: 1,
            transition: {
              delay: 1 + (offset + 2),
              duration: 0.75,
            },
          },
        }}
      >
        <div className='w-20 h-20 bg-white rounded-3xl leading-9 text-primary my-1 mx-4 flex justify-center items-center text-4xl text-center'>{winner.name}</div>
      </motion.div>
      <motion.div
        style={{
          backgroundColor: 'rgba(255, 48, 200,1)',
          borderColor: 'rgba(190,24,93,1)',
          borderTopLeftRadius: '.5rem',
          borderTopRightRadius: '.5rem',
          display: 'flex',
          filter: `opacity(${0.2 + offset / podium.length})`,
          marginBottom: -1,
          placeContent: 'center',
          width: '6rem',
        }}
        initial="hidden"
        animate="visible"
        variants={{
          hidden: { height: 0, opacity: 0 },
          visible: {
            height: 200 * (offset / podium.length),
            scale: 1,
            opacity: 1,
            transition: {
              delay: 1 + offset,
              duration: 2,
              ease: 'backInOut',
            },
          },
        }}
      >
        <span style={{ alignSelf: 'flex-end', color: 'white' }}>
          {winner.position + 1}
        </span>
      </motion.div>
    </div>
  )
}